:root {
    --primary-color: #24bcdd;
    --secondary-color: #db21f8;
    --tertiary-color: #806FEB;
    --text-color: #2a2a2a;

    --bg-light-color: #f5f8fa;
    --bg-color: #d2d6dd;
    --bg-dark-color: #929596;

    --shd: 11px 11px 11px 0px rgba(138,131,124,0.23);
    --shdi: -11px 0px 11px 0px rgba(138,131,124,0.15);
    --shdt: 11px -11px 11px 0px rgba(138,131,124,0.08);
    --shda: 0px 0px 11px 0px rgba(138,131,124,0.23);

    background-color: var(--bg-light-color);
}

.Finance {
    color: var(--text-color);
    text-align: center;
}

.content {
    max-width: 700px;
    margin: auto;
}

